.add-photo {
  width: 100%;
}

.add-photo__slogan {
  margin: 0;
  padding: 0 20px;
  padding-left: 17px;
  padding-top: 43px;
  padding-bottom: 42px;
  text-align: center;
  .setFont(@dark-blue, 14px, @regular);
  line-height: 30px;
}

.add-photo__wrapper {
  padding-top: 20px;
  background-color: @light-grey;
  display: flex;
  flex-direction: column;
}

.add-photo__photo {
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
}

.add-photo__tools-list {
  width: 320px;
  .resetList();
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  text-align: center;
  padding: 0 65px 66px 55px;
  position: relative;
  margin: 0 auto;
}

.add-photo__tools {
  border-bottom: 1px solid @grey;
  position: relative;
}

.add-photo__controls {
  padding-top: 37px;
  padding-bottom: 39px;
}

.add-photo__buttons {
  text-align: center;
  padding-left: 20px;
  padding-right: 20px;
  padding-top: 37px;
}

.add-photo__button {
  width: 280px;
  border-radius: 30px;
  line-height: 50px;
  cursor: pointer;

  &--post {
    .setFont(@pink, 18px, @bold);
    border: 3px solid @pink;
    background-color: #f2f2f2;
    margin-bottom: 8px;
  }

  &--post:hover {
    color: white;
    background-color: @pink;
  }

  &--post:active {
    color: rgba(255, 255, 255, 0.3);
    background-color: @pink;
  }

  &--cancel {
    background-color: #f2f2f2;
    .setFont(@dark-blue, 18px, @bold);
    border: 3px solid @dark-blue;
    opacity: 0.1;
  }

  &--cancel:hover {
    opacity: 0.3;
  }

  &--cancel:active {
    background-color: @dark-blue;
    color: rgba(255, 255, 255, 0.3);
    opacity: 0.3;
  }
}

@media (min-width: 660px) {
  .add-photo {
    background-color: @light-grey;
  }

  .add-photo__wrapper {
    width: 660px;
    margin: 0 auto;
    padding: 56px 20px 50px;
    display: flex;
    flex-direction: row;
  }

  .add-photo__slogan {
    background-color: white;
    padding-top: 45px;
    padding-bottom: 69px;
    font-size: 18px;
  }

  .add-photo__image {
    width: 300px;
    height: 400px;
  }

  .add-photo__photo {
    padding: 0;
    display: flex;
  }

  .add-photo__controls {
    padding: 0 0 0 35px;
    display: flex;
    flex-direction: column;
    flex-grow: 1;
  }

  .add-photo__tools {
    display: flex;
    flex-grow: 1;
    border: none;
  }

  .add-photo__buttons {
    margin: 0 auto;
    width: 260px;
    flex-grow: 1;
    padding: 0;
    padding-top: 14px;
    padding-bottom: 6px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  .add-photo__button {
    width: 260px;
    line-height: 45px;
    padding-right: 14px;
    font-family: "Open Sans", sans-serif;

    &--post {
      margin-bottom: 0;
    }
  }

  .add-photo__tools-list {
    display: flex;
    width: 100%;
    flex-direction: column;
    justify-content: flex-start;
    padding: 8px 0 0 11px;
  }

  .add-photo__item {
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 0 0 22px;
    margin-bottom: 25px;
    border-bottom: 1px solid rgba(0, 0, 0, 20%);
  }

  .add-photo__item:nth-of-type(3) {
    padding-bottom: 24px;
  }
}

@media (min-width: 960px) {
  .add-photo {
    width: 960px;
    margin: 0 auto;
    padding: 0 10px;
    background-color: white;
  }

  .add-photo__slogan {
    padding-top: 3px;
    padding-bottom: 66px;
    font-size: 18px;
  }

  .add-photo__controls {
    justify-content: flex-start;
    padding-left: 77px;
  }

  .add-photo__wrapper {
    width: auto;
    padding: 57px 60px 54px 80px;
    margin-bottom: 30px;
  }

  .add-photo__tools {
    flex-grow: 0;
  }

  .add-photo__tools-list {
    padding: 0 0 0 11px;
    margin-top: -4px;
  }

  .add-photo__item {
    margin-left: -3px;
    padding-bottom: 11px;
    padding-right: 30px;
    margin-bottom: 15px;
    justify-content: space-between;
  }

  .add-photo__item:nth-of-type(2) {
    margin-left: 3px;
    padding-bottom: 9px;
    margin-bottom: 11px;
  }

  .add-photo__item:nth-of-type(3) {
    margin-left: -6px;
    padding-bottom: 10px;
  }

  .add-photo__buttons {
    width: 410px;
    margin-left: 12px;
    flex-grow: 0;
    padding-top: 132px;
    flex-direction: row;
    justify-content: space-between;
  }

  .add-photo__button {
    width: 183px;
    margin: 0;
    padding-right: 0;
  }

  .add-photo__post {
    padding-right: 0;
  }
}
