.tariff {
  width: 276px;
  display: table;
  background-color: white;
  border-collapse: collapse;
  border: 2px solid @grey;
  border-bottom: none;
  margin: 0;
  margin-right: 1px;
  position: relative;

  &:nth-child(3)::after {
    content: "";
    top: -2px;
    left: -4px;
    position: absolute;
    width: 74px;
    height: 73px;
    z-index: 0;
    background: url("../img/bg-hit.svg");
  }

  &:nth-child(3)::before {
    content: "Хит";
    position: absolute;
    font-size: 14px;
    text-transform: uppercase;
    top: 17px;
    left: 5px;
    z-index: 1;
    color: white;
    transform: rotate(-45deg);
  }
}

.tariff__caption {
  display: table-caption;
  background-color: @dark-blue;
  padding-top: 10px;
  padding-bottom: 10px;
  text-align: center;
}

.tariff__name {
  .setFont(white, 18px, @bold);
  text-transform: uppercase;
  line-height: 30px;
  padding: 0;
  padding-top: 7px;
  margin: 0;
}

.tariff__cost {
  .setFont(white, 23px, @light);
  line-height: 30px;
  padding: 0;
  padding-top: 2px;
  padding-right: 1px;
  padding-bottom: 10px;
  margin: 0;
}

.tariff__row {
  .resetList();
  display: table-row;
  border-bottom: 2px solid @grey;
  line-height: 59px;
  vertical-align: middle;
}

.tariff__cell {
  display: table-cell;
}

.tariff__option {
  .setFont(@dark-blue, 14px, @regular);
  text-transform: uppercase;
  padding: 0;
  padding-left: 18px;
  margin: 0;
}

.tariff__value {
  text-align: right;
  padding-right: 19px;
}

.tariff__value-image {
  vertical-align: middle;
}

.tariff--full {
  display: none;
}

@media (min-width: 660px) {
  .tariff {
    margin: 0;
    width: 153px;
    overflow: hidden;

    &--full {
      display: table;
      border-right: none;
    }

    &--full .tariff__caption {
      visibility: hidden;
    }

    &:nth-child(3)::after {
      content: "";
      top: -8px;
      left: 0;
      position: absolute;
      width: 61px;
      height: 67px;
      z-index: 0;
      background: url("../img/bg-hit.svg");
      background-size: cover;
    }

    &:nth-child(3)::before {
      content: "Хит";
      position: absolute;
      font-size: 14px;
      text-transform: uppercase;
      top: 10px;
      left: 8px;
      z-index: 1;
      color: white;
      transform: rotate(-45deg);
    }
  }

  .tariff__option {
    padding-left: 14px;
    text-transform: none;
    font-size: 14px;
    line-height: 45px;

    &--hidden {
      display: none;
    }
  }

  .tariff__name {
    line-height: 30px;
    font-size: 16px;
    padding-top: 2px;
    padding-left: 2px;
  }

  .tariff__cost {
    line-height: 25px;
    padding-left: 4px;
    padding-top: 0;
    padding-bottom: 1px;
    font-size: 18px;
  }

  .tariff:nth-of-type(3) {
    border-left: none;
    border-right: none;
  }

  .tariff:nth-of-type(2) .tariff__caption {
    border-right: 2px solid @grey;
  }

  .tariff:nth-of-type(4) .tariff__caption {
    border-left: 2px solid @grey;
  }

  .tariff__value {
    text-align: center;
    padding: 0;
    line-height: 45px;
  }

  .tariff__value-image {
    vertical-align: middle;
  }
}

@media (min-width: 960px) {
  .tariff {
    width: 233px;

    &:nth-child(3)::after {
      content: "";
      top: -2px;
      left: 0;
      position: absolute;
      width: 74px;
      height: 73px;
      z-index: 0;
      background: url("../img/bg-hit.svg");
      background-size: cover;
    }

    &:nth-child(3)::before {
      content: "Хит";
      position: absolute;
      font-size: 14px;
      text-transform: uppercase;
      top: 16px;
      left: 11px;
      z-index: 1;
      color: white;
      transform: rotate(-45deg);
    }
  }

  .tariff__name {
    padding-top: 5px;
    font-size: 18px;
  }

  .tariff__cost {
    padding-top: 2px;
    font-size: 24px;
    line-height: 30px;
  }

  .tariff__cell {
    line-height: 59px;
  }

  .tariff__caption {
    padding-bottom: 17px;
  }

  .tariff__option {
    padding-left: 20px;
    font-size: 18px;
    line-height: 59px;
  }
}
