html {
  box-sizing: border-box;
}
*,
*::after,
*::before {
  box-sizing: inherit;
}
body {
  font-family: "Open Sans", "Tahoma", sans-serif;
  margin: 0;
}
.visually-hidden:not(:focus):not(:active),
input[type="radio"].visually-hidden,
input[type="checkbox"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
@font-face {
  font-weight: normal;
  font-family: "Open Sans";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
}
@font-face {
  font-weight: bold;
  font-family: "Open Sans";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
}
@font-face {
  font-weight: 300;
  font-family: "Open Sans";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/opensanslight.woff2") format("woff2"), url("../fonts/opensanslight.woff") format("woff");
}
.slider__toggles {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding-bottom: 37px;
}
.slider__toggle {
  width: 12px;
  height: 12px;
  padding: 0;
  border-radius: 50%;
  background-color: #283645;
  opacity: 30%;
  border: 3px solid white;
  cursor: pointer;
}
.slider__toggle:not(.slider__toggle--current):hover {
  opacity: 60%;
}
.slider__toggle:not(.slider__toggle--current):active {
  opacity: 10%;
}
.slider__toggle--current {
  opacity: 100%;
  background-color: white;
  cursor: default;
  border: 3px solid #283645;
}
.slider__toggle:nth-child(2) {
  margin: 0 13px;
}
@media (min-width: 660px) {
  .slider__toggles {
    margin-bottom: 15px;
  }
}
.page-header {
  min-width: 100%;
  height: auto;
  background-color: rgba(0, 0, 0, 0.3);
  position: absolute;
  display: block;
}
.page-header__buttons {
  padding: 22px 20px 25px 19px;
  background-color: transparent;
  border-bottom: 1px solid black;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.page-header__list {
  flex-basis: 280px;
  padding: 0;
  margin: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
}
.page-header__item a {
  display: block;
  text-decoration: none;
  background-color: #283645;
  color: white;
  font-size: 18px;
  font-weight: 700;
  border-bottom: 1px solid black;
  box-shadow: inset 0 1px 0 0 rgba(255, 255, 255, 0.15);
  text-transform: uppercase;
  padding-top: 3px;
  line-height: 60px;
  text-align: center;
}
.page-header__item a[href]:hover {
  color: #d22856;
}
.page-header__item a[href]:active {
  color: rgba(210, 40, 86, 0.3);
}
.page-header__wrapper {
  width: 100%;
  display: flex;
  flex-direction: column;
}
.page-header__main-nav {
  background: transparent;
  min-width: 280px;
  display: none;
}
.page-header__logo {
  text-decoration: none;
  display: block;
  border: none;
  background-color: transparent;
}
.page-header__logo:hover svg {
  fill: #d22856;
}
.page-header__logo:active svg {
  fill: white;
  opacity: 0.3;
}
.page-header__image {
  display: none;
  fill: white;
}
.page-header__image--mobile {
  display: block;
}
.page-header__toggle {
  width: auto;
  padding: 0;
  background-color: transparent;
  border: none;
}
.page-header__toggle svg {
  fill: white;
}
.page-header__toggle svg:first-child {
  display: block;
}
.page-header__toggle svg:last-child {
  display: none;
}
.page-header.menu-open--default.menu-open {
  position: static;
  background-color: #283645;
}
.page-header__main-nav.menu-open--default {
  display: block;
}
.page-header__toggle.menu-open--default svg:first-child {
  display: none;
}
.page-header__toggle.menu-open--default svg:last-child {
  display: none;
  background-color: #283645;
}
.page-header.menu-open {
  position: absolute;
  background-color: #283645;
}
.page-header__main-nav.menu-open {
  display: block;
}
.page-header__toggle.menu-open svg {
  display: block;
}
.page-header__toggle.menu-open svg:first-child {
  display: none;
}
@media (min-width: 660px) {
  .page-header__main-nav {
    min-width: 660px;
  }
  .page-header__logo img {
    width: 147px;
    height: 40px;
  }
  .page-header__buttons {
    padding-top: 25px;
    padding-bottom: 42px;
    align-items: flex-end;
  }
  .page-header__image {
    display: none;
  }
  .page-header__image--tablet {
    display: block;
  }
}
