  .page-header__toggle.menu-open--default svg:first-child {
    display: none;
  }
  .page-header__toggle.menu-open--default svg:last-child {
    display: none;
    background-color: #283645;
  }
  .page-header__toggle.menu-open svg {
    display: block;
  }
  .page-header__toggle.menu-open svg:first-child {
    display: none;
  }
  .page-header,
  .page-header.menu-open.menu-open--default {
    position: absolute;
    display: block;
    height: auto;
    width: 100%;
    background-color: rgba(0, 0, 0, 0.3);
  }
  .page-header__main-nav,
  .page-header__main-nav.menu-open,
  .page-header__main-nav.menu-open--default {
    min-width: 620px;
    display: block;
  }
  .page-header__list {
    display: flex;
    flex-direction: row;
    padding-top: 15px;
    align-items: center;
    justify-content: space-between;
    flex-grow: 1;
  }
  .page-header__logo {
    background-color: transparent;
  }
  .page-header__image {
    display: none;
  }
  .page-header__image--desktop {
    display: block;
  }
  .page-header__wrapper {
    width: 960px;
    margin: 0 auto;
    padding: 23px 10px 38px 10px;
    flex-direction: row;
    align-items: center;
    background-color: transparent;
  }
  .page-header__item a {
    height: auto;
    line-height: normal;
    background-color: transparent;
    color: white;
    font-size: 18px;
    font-weight: 400;
    text-align: center;
    text-transform: none;
    padding-bottom: 9px;
    border: none;
    box-shadow: none;
  }
  .page-header__item--current a {
    border-bottom: 2px solid white;
  }
  .page-header__buttons {
    background-color: transparent;
    border-bottom: none;
    padding: 0;
    padding-right: 172px;
  }
  .page-header__toggle {
    display: none;
  }
}
