.tariff {
  width: 276px;
  display: table;
  background-color: white;
  border-collapse: collapse;
  border: 2px solid #e5e5e5;
  border-bottom: none;
  margin: 0;
  margin-right: 1px;
  position: relative;
}
.tariff:nth-child(3)::after {
  content: "";
  top: -2px;
  left: -4px;
  position: absolute;
  width: 74px;
  height: 73px;
  z-index: 0;
  background: url("../img/bg-hit.svg");
}
.tariff:nth-child(3)::before {
  content: "Хит";
  position: absolute;
  font-size: 14px;
  text-transform: uppercase;
  top: 17px;
  left: 5px;
  z-index: 1;
  color: white;
  transform: rotate(-45deg);
}
.tariff__caption {
  display: table-caption;
  background-color: #283645;
  padding-top: 10px;
  padding-bottom: 10px;
  text-align: center;
}
.tariff__name {
  color: white;
  font-size: 18px;
  font-weight: 700;
  text-transform: uppercase;
  line-height: 30px;
  padding: 0;
  padding-top: 7px;
  margin: 0;
}
.tariff__cost {
  color: white;
  font-size: 23px;
  font-weight: 300;
  line-height: 30px;
  padding: 0;
  padding-top: 2px;
  padding-right: 1px;
  padding-bottom: 10px;
  margin: 0;
}
.tariff__row {
  padding: 0;
  margin: 0;
  list-style: none;
  display: table-row;
  border-bottom: 2px solid #e5e5e5;
  line-height: 59px;
  vertical-align: middle;
}
.tariff__cell {
  display: table-cell;
}
.tariff__option {
  color: #283645;
  font-size: 14px;
  font-weight: 400;
  text-transform: uppercase;
  padding: 0;
  padding-left: 18px;
  margin: 0;
}
.tariff__value {
  text-align: right;
  padding-right: 19px;
}
.tariff__value-image {
  vertical-align: middle;
}
.tariff--full {
  display: none;
}
@media (min-width: 660px) {
